import React, { useState } from 'react';
import Products from './products';
import './App.css';

function App() {
  const [isInput, setInput] = useState(false);
  const [country, setCountry] = useState('');
  const [selectedBrand, setBrand] = useState('');
  const [brandFilter, setBrandFilter] = useState([]);
  return (
    <div className='container-fluid'>
        <div className='row searchbar'>
          <div className="col-sm-12 col-md-6">
            <div className="row">
              <span className="float-right">कौन सा देश बनाता है?</span>
            </div>
            <div className='row'>
              <span className="float-right">Which country makes?</span>
            </div>
          </div>
          <div className="col-sm-12 col-md-6">
            <input type="text" class="search_input float-left" value={selectedBrand} name="search"  onChange={findProduct} autocomplete="off" autofocus="" />
          </div>
        </div>
      {isInput ? <div>
        <ul>
          {
            brandFilter.length > 0 ? <div>
              {brandFilter.map((brand, i) => {
                return (
                  <li key={i}>
                    <button value={brand} onClick={(e) => selectBrand(e)}>
                      {brand}
                    </button>
                  </li>
                )
              }
              )}
            </div> :
              <li className='notListed'>Not Listed</li>
          }
        </ul>
      </div> : ''}

      <div className="row main-content">
       
          <div className={`inner`}>
            {
              <div class="col-sm-12">
                <div class="row">

                  <div class={`offset-md-4 offset-lg-4 col-md-4 col-lg-4 col-sm-12  ${country === 'india' ? 'active' : 'inactive'}`}>
                    <div class="card">
                      <img class="card-img-top" src="./india-flag.svg" alt="Made In India"/>
                      <div class="card-body">
                        <p class="card-text">{`भारत में निर्मित - ${country === 'india' ? 'हां' : 'नहीं'}`}</p>
                        <p class="card-text">{`Made in India - ${country === 'india' ? 'Yes' : 'No'}`}</p>
                      </div>
                    </div>
                  </div>

                  <div class={`col-lg-4 col-md-4 col-sm-12  ${country === 'china' ? 'active' : 'inactive'}`}>
                    <div class="card">
                      <img class="card-img-top" src="./china-flag.svg" alt="Made In China"/>
                      <div class="card-body">
                        <p class="card-text">{`चीन में निर्मित - ${country === 'china' ? 'हां' : 'नहीं'}`}</p>
                        <p class="card-text">{`Made in China - ${country === 'china' ? 'Yes' : 'No'}`}</p>
                      </div>
                    </div>
                  </div>

                  <div class={`col-lg-4 col-md-4 col-sm-12  ${(country && country !== 'china' && country !== 'india') ? 'active' : 'inactive'}`}>
                    <div class="card">
                      <img class="card-img-top" src="./somalia-flag.svg" alt="Made Elsewhere"/>
                      <div class="card-body">
                        <p class="card-text">{`अन्य देश में निर्मित - ${(country !== 'china' && country !== 'india') ? 'हां' : 'नहीं'}`}</p>
                        <p class="card-text">{`Made Elsewhere - ${(country !== 'china' && country !== 'india') ? 'Yes' : 'No'}`}</p>
                      </div>
                    </div>
                  </div>

                </div>
              </div>
            }
          </div> 

      </div>
      
    
      <footer class="">
        <div class="container">

          <div class="btn-group-vertical" role="group" aria-label="Button group with nested dropdown">
            <button type="button" class="btn btn-secondary">
              <a class="footer-link" href="https://docs.google.com/forms/d/e/1FAIpQLSd4NnHXtvv7DpY93-6ZcwUk1tkvL78eT6tSyDNDdT3lY2j0hw/viewform" target="_blank" rel="noopener noreferrer">किसी भी गलत जानकारी की सूचना दें (Report any incorrect data)</a>
            </button>
            <button type="button" class="btn btn-secondary">
              <a class="footer-link" href="https://docs.google.com/forms/d/e/1FAIpQLSfOLjHq0Crnm_6izV9N2WNO9NP9DwYttsZKg9Iqa3j-a5LYOA/viewform" target="_blank" rel="noopener noreferrer">कंपनी / उत्पाद जोड़ने का अनुरोध (Company/Product add request)</a>
            </button>
            <button type="button" class="btn btn-secondary">
              <a class="footer-link" href="https://docs.google.com/forms/d/e/1FAIpQLSfckly2X5CcO-1cEKMBVWDJtJZvjrPW3ivaBb3zvHCCm3bbmg/viewform" target="_blank" rel="noopener noreferrer">सुझाव या शिकायत दर्ज करें (Suggestions or Complaints)</a>
            </button>
          </div>
            
          
        </div>
      </footer>

    </div>
  );

  function selectBrand(e) {
    setBrand(e.target.value);
    let brands = Products.brands;
    let text = e.target.value.toLowerCase();
    for (let [key, value] of Object.entries(brands)) {
      if (text === key.substr(0, text.length)) {
        setCountry(value.country);
      }
    }
    setInput(false);

  }

  function findProduct(e) {
    setBrand(e.target.value);
    let brandFilter = []
    if (e.target.value) {
      setInput(true);
    } else { setInput(false) }
    let brands = Products.brands;
    let text = e.target.value.toLowerCase();
    for (let [key] of Object.entries(brands)) {
      if (text === key.substr(0, text.length)) {
        brandFilter.push(key)
      }
    }
    setBrandFilter(brandFilter);
  }
}

export default App;
